import { Component, Input, OnInit } from '@angular/core';
import { RBACINFO } from './pics-ocrvalidation/@core/ocr-validation-url.config';
import { PermissionStore } from './pics-ocrvalidation/store/permission.store';
import { DataStoreService } from './pics-ocrvalidation/service/data-store.service';
import { Observable } from 'rxjs';

@Component({
  selector: 'lib-ocr-validations',
  template: `
   <lib-ocrvalidation></lib-ocrvalidation>
  `,
  styles: [
  ]
})
export class OcrValidationsComponent implements OnInit {
  @Input() public RBACORG?: RBACINFO = new RBACINFO();
  @Input() public PERMISSION?: any;
  @Input() public ocrEvent!: Observable<any>;
  constructor( private permissionStore: PermissionStore,
    private _storeservice: DataStoreService) { 

    }

 ngOnInit() {
    this.ocrEvent.subscribe((val: any) => {
      this.RBACORG = val.RBACORG;
      this.PERMISSION = val.PERMISSION;
      this._storeservice.setData('RBACORG', this.RBACORG);
      this.permissionStore.setStore(this.PERMISSION);
    })
  }

}
