
import { Component, EventEmitter, Injector, Input, OnInit, Output, ViewChild } from '@angular/core';
import { AlertService } from '../service/alert.service';
import { AuthService } from '../service/auth.service';
import { DataStoreService } from '../service/data-store.service';
import { LocalService } from '../service/local.service';
// import { FormioComponent } from 'angular-formio';
import { NgxfUploaderService } from 'ngxf-uploader';
import { OcrValidationService } from '../@core/ocr-validation.service';
import { Subscription } from 'rxjs';
import { RBACINFO } from '../@core/ocr-validation-url.config';

@Component({
  selector: 'lib-ocrvalidation',
  templateUrl: './ocrvalidation.component.html',
  styleUrls: ['./ocrvalidation.component.scss']
})
export class OcrvalidationComponent implements OnInit {
  ocrDocumentDetails: any;
  imgUrl: any;
  showOcrForm!: boolean;
  tableschemaconfig: any;
  jsonForm: any;
  isformIO = false;
  @ViewChild('formIO')
  // formIO!: FormioComponent;
  formJson: Array<any> = [];
  environment: any;
  RBACORG: RBACINFO = new RBACINFO();
  orgSubs!: Subscription;
  orgId: any;
  triggerRefresh: any;
  authService: any;
  localstorage: any;
  dataStore: any;
  uploadService: any;
  alertService: AlertService;
  fromTitle: any;
  duplicateTableSchemaconfig: any;
  isOcrForm: any;
  documentType: any;
  sourceid: any;
  isReadOnly = false;
  contentArray: Array<any> = [];
  @Input() formResponseData: any;
  @Input() currenttemplateResult: any;
  @Input() submitionData: any;
  @Output() ocrResponse = new EventEmitter();
  constructor(injector: Injector,private ocrValidationService: OcrValidationService, private _storeservice: DataStoreService) {
    this.authService = injector.get<AuthService>(AuthService);
    this.localstorage = injector.get<LocalService>(LocalService);
    this.dataStore = injector.get<DataStoreService>(DataStoreService);
    this.uploadService = injector.get<NgxfUploaderService>(NgxfUploaderService);
    this.alertService = injector.get<AlertService>(AlertService);
    this.triggerRefresh = new EventEmitter();
   }

   ngOnInit(): void {
    this.orgSubs =  this._storeservice.currentStore.subscribe((res: any) => {
      if (res['RBACORG'] && res['RBACORG'] !== '') {
        this.RBACORG = res['RBACORG'];
        console.log(this.RBACORG, 'RBACORG Profile');
        this.environment = this.RBACORG['environment'];
        this.orgId = parseInt(this.RBACORG['orgID']);
        if(this.environment){
          const obj = this.localstorage.getObj('OCRObj');
          this.getTemplate(this.currenttemplateResult);
          this.getUpload(obj);
        }
      }
    });
  }
ngOnDestroy(): void {
   
    this.orgSubs.unsubscribe();
  }
  private getTemplate(result: any) {
    if (result) {
      this.fromTitle = result.data?.pagename ? result.data?.pagename : '';
      this.dataStore.setData('title', this.fromTitle);

      if (result.data.templatejson) {
        this.isformIO = true;
        result.data.templatejson = result.data.templatejson.replaceAll('{sourceid}', this.sourceid);
        this.tableschemaconfig = JSON.parse(result.data.tableschemaconfig);
        this.duplicateTableSchemaconfig = { ...this.tableschemaconfig };
        this.isOcrForm = this.tableschemaconfig.ocrchecked;
        this.documentType = this.tableschemaconfig.selectedDocumentType;
        this.jsonForm = {
          components: JSON.parse(result.data.templatejson).components.filter((component:any) => component.key !== 'submit')
        };
      }
    }
  }

  getUpload(obj:any) {
    this.ocrValidationService.getUpload(obj).subscribe(
      (res: any) => {
        if (res && res.data) {
          const resData = res.data;
          const imageCategory = resData?.imageCategory;
          this.ocrDocumentDetails = imageCategory?.id_json[0];
          const fileUrl = resData?.fileUrl;
          this.imgUrl = fileUrl?.source.url;
          this.ocrFormPatching();
        } else {
          this.alertService.error('Something Went Wrong!');
        }
      },
      err => console.log(err)
    );
  }

  ocrFormPatching() {
    const ArrayOCR = this.tableschemaconfig.fieldmapping.filter((element:any) => element.ocrkey);
    ArrayOCR?.forEach((res:any) => {
      if (this.ocrDocumentDetails) {
        const documentValue = Object.keys(this.ocrDocumentDetails);
        documentValue?.forEach(element => {
          if (element.toLowerCase() == res.ocrkey.toLowerCase()) {
            this.submitionData.data[res.field] =
              res.ocrkey == 'date_of_birth'
                ? new Date(this.ocrDocumentDetails[element])
                : this.ocrDocumentDetails[element];
          }
        });
      }
    });
    const json = this.jsonForm.components[0].components;
    // ArrayOCR.forEach(({ field }) => {
    //   const formJson = json.filter(res => res.key.includes(field));
    //   this.formJson.push(...formJson);
    // });
    this.jsonForm.components[0].components = this.formJson;
    this.triggerRefresh.emit({
      property: 'form',
      value: this.jsonForm
    });
  }

  backToForm() {
    this.ocrResponse.emit(this.submitionData);
  }

}
